*{
    margin: 0;
    padding: 0;
  }
  
.login-box {
    width: 350px;
    height: 350px;
    padding: 50px;
    padding-top: 35px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background: rgb(245, 245, 245);
    text-align: start;
    border-radius: 8px;
    border: 1px solid rgb(176, 175, 175);
}

.h2log {
    display: inline-block;
    margin: 0px 0px 15px 15px;
    padding: 10px 30px;
    background-color: rgba(255, 249, 243, 0.95);
    color:#FF7D34;
    font-size: 18px;
    font-weight: 500;
    border-radius: 15px 15px 15px 0;
}

.h2reg {
    display: inline-block;
    padding: 10px 30px;
    background-color: rgba(255, 249, 243, 0.559);
    color: #FF7D34;
    font-size: 18px;
    font-weight: 500;
    border-radius: 15px 15px 15px 0;
}

label {
    margin: 15px;
    font-size: 14px;
    font-weight: 500;
    color: #0E0E73;
}

.login-box input[type="text"], .login-box input[type="email"], .login-box input[type="password"] {
    border: 0;
    background: none;
    display: block;
    margin: 5px auto;
    text-align: start;
    border: 1px solid lightgrey;
    padding: 12px 10px;
    width: 300px;
    outline: none;
    color: black;
    border-radius: 5px;
}

.login-box input[type="text"]:focus, .login-box input[type="email"]:focus, .login-box input[type="password"]:focus {
    border-color: grey;
}

.login-box input[type="submit"] {
    border: 0;
    background-color: #FF7D34;
    font-weight: 700;
    display: block;
    margin: 20px auto;
    text-align: center;
    padding: 10px 10px;
    width: 325px;
    outline: none;
    color: rgba(255, 249, 243, 0.95);
    border-radius: 15px 15px 15px 0;
    transition: 0.25s;
    cursor: pointer;
}

.login-box input[type="submit"]:hover {
    background: #0E0E73;

}

.notregistered {
    text-align: end;
    font-size: 12px;
    margin-bottom: 35px;
}

.notregistered a {
    color: #0E0E73;
}

.forgotpassword a {
    color: #0E0E73;
    font-size: 12px;
    margin: 20px;
}
.container {
    width: 230px;
    height: 500px;
    margin: 40px;
    padding: 20px;
    border-radius: 5px;
    background-color: #fff9f313;
    border: 1px solid rgb(176, 175, 175);
}

.editbtn{
    height: 30px;
    width: 70px;
    padding: 5px 25px;
    color: #0E0E73;
    background-color: #FFF9F3;
    border: none;
    border-radius: 25px 25px 25px 0px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
}

.profileimg {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.card-body{
    color: #FF7D34;
}

.process{
    color: #FF7D34;
}





.layout {
    display: grid;
    min-height: 100vh;
    grid-template-rows: auto 1fr auto;
    background-image: url("/static/media/Background 2.62d6ef7b.png");
}

.App-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 10%;
}
.nav {
    padding: 2em;
    text-align: center;
    border-bottom: none;
}

li {
    text-align: center;
    text-decoration: none;
    padding: 0px;
    margin: 0px 40px;
}

.navlinks {
    list-style: none;
}

.navlinks li {
    display: inline-block;
    padding: 0px 20px;
}

.navlinks li a {
    transition: all 0.3s ease 0s;
    color: #0E0E73;
}

.navlinks li a:hover {
    font-weight: 900;
}

.footer {
    box-sizing: border-box;
    margin: 0;
    padding: 2.5em;
    text-align: center;
}

li, a {
    text-decoration: none;
}

.footerlinks {
    list-style: none;
}

.footerlinks li {
    display: inline-block;
    padding: 0px 20px;
}

.footerlinks li a {
    transition: all 0.3s ease 0s;
    color: #0E0E73;
}

.footerlinks li a:hover {
    font-weight: 900;
}
